@use '../base/variables' as var;
@use '../base/mixins' as mix;
@use 'sass:color';

.site-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10; // asegúrate de que sea mayor que el ::before del hero
  background: transparent; // elimina el fondo sólido
  border: none; // opcional si tenías borde
}

.secondary{
    position: relative;
    background-color: var.$dark;
}

.brand{
  max-width: 50%;
}

.brand img{
    max-width: 35%;
}

.site-header .nav{ 
    display:flex; 
    align-items:center; 
    justify-content:space-between; 
    height:72px; 
    gap:1rem;
    border-block-end: 1px solid var.$white; 
}

.brand--logo{ 
    text-decoration:none; 
    color:inherit; 
}

.brand__text{ 
    font-weight:var.$black-f; 
    letter-spacing:.3px; 
    font-size:1.25rem; 
    color:#0f172a; 
}

.brand__tag{ 
    color:var.$muted; 
    font-weight:var.$regular; 
    margin-left:.5rem; 
}

.menu {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* === Enlaces normales (sin clase) === */
.menu a:not(.btn) {
  position: relative;
  text-decoration: none;
  color: var.$white;
  padding: 0.5rem 0.8rem;
  transition: transform 0.3s ease-in-out;
}

.menu a:not(.btn)::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0%;
  background: var.$accent-2;
  transition: width 0.3s ease-in-out;
}

.menu a:not(.btn):hover {
  transform: translateX(4px);
}

.menu a:not(.btn):hover::after {
  width: 100%;
}

/* ===  Botón especial (.btn) === */
.menu .btn {
    background-color: var.$accent-2;
    padding: 0.5rem 1rem;
    border-radius: 999px;
    transition: background 0.3s ease-in-out, transform 0.3s ease-in-out;
    color: var.$white;
    text-decoration: none;
}

/* Efecto distinto para el botón: brillo al pasar */
.menu .btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  transition: left 0.4s ease-in-out;
  border-radius: inherit;
}

.menu .btn:hover {
    background-color: var.$dark;
    transform: scale(1.05);
}

.menu .btn:hover::before {
  left: 100%; /* brillo pasa de izquierda a derecha */
}


.nav-toggle{ 
    display:none; 
}

@media (max-width: 900px){
    .nav-toggle{ 
        display:block; 
        background:none; 
        border:1px solid var.$border; 
        padding:.4rem .6rem; 
        border-radius:8px; 
    }

    .menu{ 
        position:fixed; 
        inset: 72px 0 auto 0; 
        background:var.$white; 
        padding: 1rem; 
        border-bottom: 1px solid var.$border; 
        transform: translateY(-120%); 
        transition:.25s; 
        flex-direction:column; 
        align-items:flex-start; 
    }
    
    .menu.open{ 
        transform:translateY(0); 
    }
}
