  background-color: #f9fafb;
  transform: translateY(-1px);
  box-shadow: 0 8px 24px rgba(148, 163, 184, 0.35);
}

.tickets-table__cell-id {
  font-weight: 600;
  color: #9ca3af;
}

.tickets-table__cell-title {
  max-width: 270px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.tickets-table__empty {
  text-align: center;
  padding: 24px 8px;
  color: #9ca3af;
}

.priority-pill {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 4px 10px;
  border-radius: 999px;
  font-size: 0.78rem;
  font-weight: 600;
}

.priority-pill--high {
  background-color: #fef3c7;
  color: #92400e;
}

.priority-pill--medium {
  background-color: #fee2e2;
  color: #b91c1c;
}

.priority-pill--low {
  background-color: #dcfce7;
  color: #166534;
}

.priority-pill--normal {
  background-color: #e5e7eb;
  color: #374151;
}

.status-pill {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 4px 12px;
  border-radius: 999px;
  font-size: 0.78rem;
  font-weight: 600;
}

.status-pill--open {
  background-color: #dbeafe;
  color: #1d4ed8;
}

.status-pill--in-progress {
  background-color: #fef3c7;
  color: #92400e;
}

.status-pill--resolved {
  background-color: #dcfce7;
  color: #166534;
}

.status-pill--closed {
  background-color: #e5e7eb;
  color: #374151;
}

.tickets-table__pagination {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.page-btn {
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background-color: #f9fafb;
  padding: 4px 8px;
  cursor: pointer;
  font-size: 0.85rem;
}

.page-current {
  font-size: 0.85rem;
  font-weight: 600;
}

@media (max-width: 768px) {
  .tickets-page {
    padding: 16px;
  }
  .tickets-toolbar {
    flex-direction: column;
    align-items: stretch;
  }
  .tickets-toolbar__search {
    max-width: 100%;
  }
  .tickets-table-card {
    padding: 12px;
  }
}
.tickets-table__row {
  border-bottom: 1px solid #f1f5f9;
  transition: background-color 0.12s ease, transform 0.05s ease, box-shadow 0.12s ease;
  cursor: default;
}
.tickets-table__row--clickable {
  cursor: pointer;
}
.tickets-table__row:hover {
  background-color: #f9fafb;
  transform: translateY(-1px);
  box-shadow: 0 8px 24px rgba(148, 163, 184, 0.35);
}